import React, {useEffect, useState} from "react";
import styled from "styled-components";
import Page from "../component/Page";
import tripsList from "../api/trip";
import {Col, Row} from "react-bootstrap";
import {Heading3} from "../component/Heading";
import {PhotoFrame} from "../component/PhotoFrame";
import Calendar from "../component/Calendar";
import {Link} from "react-router-dom";
import clock from "../api/clock";

const TripPhotoFrame = styled.div`
  margin-bottom: 20px;
`;

export default function TripListPage() {
  const [trips, setTrips] = useState([]);
  const [tripsByYear, setTripsByYear] = useState({});

  useEffect(() => setTrips(Object.keys(tripsList).map(key => tripsList[key])), [tripsList]);
  useEffect(() => setTripsByYear(groupByYear(trips)), [trips]);

  return (
    <Page title="Viagens">
      {Object.keys(tripsByYear).sort((yearA, yearB) => yearB - yearA).map(year =>
        <div key={year}>
          <Heading3>{year}</Heading3>
          {tripsByYear[year].map(toTrip)}
        </div>
      )}
    </Page>
  );
}

function toTrip(trip) {
  return (
    <Row key={trip.date}>
      <Col xs={2} lg={1}>
        <Calendar date={trip.date} month />
      </Col>
      <Col xs={10} md={5} lg={4}>
        <TripPhotoFrame>
          <Link to={`/viagem/${trip.url}`}>
            <PhotoFrame src={trip.banner} alt={trip.title} />
          </Link>
        </TripPhotoFrame>
      </Col>
      <Col xs={12} md={5} lg={7}>
        <Heading3><Link to={`/viagem/${trip.url}`}>{trip.title}</Link></Heading3>
        <p>{trip.ad.text}</p>
      </Col>
    </Row>
  );
}

function groupByYear(trips) {
  console.log('trips', trips);

  const tripsByYear = {};

  trips.forEach(trip => {
    const year = clock(trip.date, 'YYYY-MM-DD').format('YYYY');

    const list = tripsByYear[year] || [];
    list.push(trip);
    tripsByYear[year] = list;
  });

  console.log('trips by year', tripsByYear);
  return tripsByYear;
}