import React, {useEffect, useState} from "react";
import styled from "styled-components";
import clock from "../api/clock";
import Page from "../component/Page";
import Price from "../component/Price";
import {Button, Carousel, Col, Row, Tab, Tabs} from "react-bootstrap";
import {FontAwesomeIcon} from "@fortawesome/react-fontawesome";
import {faCalendarCheck, faClock, faImages, faMap, faThumbsDown, faThumbsUp} from "@fortawesome/free-regular-svg-icons";
import {
  faCaretDown,
  faCaretUp,
  faCheckCircle,
  faConciergeBell,
  faFlag,
  faInfoCircle,
  faLeaf,
  faLongArrowAltRight,
  faMapMarkedAlt,
  faMapSigns,
  faParking,
  faPaw,
  faPlane,
  faPlaneArrival,
  faPlaneDeparture, faSpa,
  faSwimmer,
  faTimesCircle,
  faUser,
  faUtensils,
  faWifi
} from "@fortawesome/free-solid-svg-icons";
import {faExternalLinkAlt} from "@fortawesome/free-solid-svg-icons/faExternalLinkAlt";
import Calendar from "../component/Calendar";
import {Event, Timeline} from "../component/Timeline";
import {Jumbotron} from "../component/Components";
import {ElderlyGroup, FamilyGroup, PairGroup} from "../component/Group";
import trips from "../api/trip";
import {PhotoFrame} from "../component/PhotoFrame";
import {useParams} from "react-router-dom";

const Title = styled.h3`
  margin-top: 40px;
`;

const List = styled.ul`
  list-style: none;
  padding: 0;
  margin: 0;
`;

const AmenitiesList = styled.ul`
  
`;

const AmenityItem = styled.li`
  
`;

const IncludingIcon = styled(FontAwesomeIcon)`
  color: #007700;
`;

const NotIncludingIcon = styled(FontAwesomeIcon)`
  color: #aa0000;
`;

const Box = styled.div`
  margin-top: 20px;
`;

const SubTitle = styled.h5`
  font-family: Caveat;
  color: #169be9;
`;

const ExternalReview = styled.div`
  margin-top: 15px;
  padding: 10px;
  background-color: #fcfcfc;
  border: solid 1px #e4e4e4;
  border-top: solid 10px #e4e4e4;
`;

const ExternalReviewText = styled.p`
  margin: 0;
  font-family: Caveat;
  color: #666;
`;

const ExternalReviewAuthor = styled.p`
  margin: 0;
  font-size: 14px;
  text-align: right;
`;

const ExternalReviewSource = styled.p`
  margin-bottom: 0;
  font-style: italic;
  font-size: 12px;
`;

const Restaurant = styled.div`
  
`;

const ArrowRight = styled(FontAwesomeIcon)`
  margin-right: 10px;
  font-size: 32px;
`;

const TripDate = styled.h5`
  & small {
    font-style: italic;
    font-size: 70%;
    color: #ff9603;
  }
`;

const Source = styled.p`
  font-style: italic;
  text-align: right;
`;

const RecommendedTo = styled.p`
  font-family: Caveat;
  text-align: right;
`;

const Banner = styled.div`
  margin: 20px 0;

  & img {
    width: 100%;
  }
`;

export default function TripPage(props) {
  const [trip, setTrip] = useState();
  const [days, setDays] = useState(1);
  const [showFullSchedule, setShowFullSchedule] = useState(false);

  useEffect(() => setTrip(trips[`${props.place}_${props.date}`]), [props.place, props.date]);
  useEffect(() => trip && setDays(clock(trip.to, 'YYYY-MM-DD').diff(clock(trip.from, 'YYYY-MM-DD'), 'days')), [trip, setDays]);

  return (
    <div>
      {trip && <Page title={trip.title}>
        <Row>
          <Col>
            <TripDate>{clock(trip.date, 'YYYY-MM-DD').format('LL')} <small>({clock(trip.date, 'YYYY-MM-DD').fromNow()})</small></TripDate>
          </Col>
          <Col>
            <div style={{textAlign: 'right'}}>
              <div style={{display: 'inline-block', textAlign: 'center'}}>
                <Calendar date={trip.date} month />
                {trip.to && <ArrowRight icon={faLongArrowAltRight} />}
                {trip.to && <Calendar date={trip.to} month />}
                {trip.to && <p>{days} dia{days > 1 && 's'}</p>}
              </div>
            </div>
          </Col>
        </Row>
        {trip.banner && <Banner><PhotoFrame src={trip.banner} alt={trip.title} /></Banner>}
        {trip.ad && <Jumbotron>
          {trip.ad.subtitle && <h4>{trip.ad.subtitle}</h4>}
          {trip.ad.title && <h3>{trip.ad.title}</h3>}
          <p>{trip.ad.text}</p>
          {trip.ad.source && <Source><a href={trip.ad.source.link} target="_blank" rel="noopener noreferrer">{trip.ad.source.label}</a></Source>}
        </Jumbotron>}
        {trip.recommendedTo && trip.recommendedTo.length > 0 && <Box>
          <RecommendedTo>Recomendado para {trip.recommendedTo.map(group => toGroup(group))}</RecommendedTo>
        </Box>}
        {trip.sightseeing && trip.sightseeing.length > 0 && <Box>
          <Title><FontAwesomeIcon icon={faMapSigns} /> Explore</Title>
          {toSightseeing(trip.sightseeing)}
        </Box>}
        {trip.restaurants && trip.restaurants.length > 0 && <Box>
          <Title><FontAwesomeIcon icon={faUtensils} /> Saboreie</Title>
          {toRestaurants(trip.restaurants)}
        </Box>}
        {trip.hotels && trip.hotels.map(hotel => toHotel(hotel))}
        {trip.flights && trip.flights.length > 0 && <Box>
          <Title><FontAwesomeIcon icon={faPlane} /> Aéreo</Title>
          {toFlights(trip.flights)}
        </Box>}
        {trip.schedule && <Box>
          <Title><FontAwesomeIcon icon={faCalendarCheck} /> Programação</Title>
          <p>A programação poderá ser alterada para proporcionar uma melhor experiência aos passageiros.</p>
          {!showFullSchedule && <Button variant="secondary" onClick={() => setShowFullSchedule(true)} size="sm"><FontAwesomeIcon icon={faCaretDown} /> Ver programação completa</Button>}
          {showFullSchedule && <Button variant="secondary" onClick={() => setShowFullSchedule(false)} size="sm"><FontAwesomeIcon icon={faCaretUp} /> Esconder programação completa</Button>}
          {showFullSchedule && toSchedule(trip.schedule)}
        </Box>}
        <Row>
          {trip.including && trip.including.length > 0 && <Col xs={12} md={6}>
            <Box>
              <SubTitle><FontAwesomeIcon icon={faThumbsUp} /> Incluso</SubTitle>
              <List>
                {toList(trip.including, <IncludingIcon icon={faCheckCircle} />)}
              </List>
            </Box>
          </Col>}
          {trip.notIncluding && trip.notIncluding.length > 0 && <Col xs={12} md={6}>
            <Box>
              <SubTitle><FontAwesomeIcon icon={faThumbsDown} />  Não Incluso</SubTitle>
              <List>
                {toList(trip.notIncluding, <NotIncludingIcon icon={faTimesCircle} />)}
              </List>
            </Box>
          </Col>}
        </Row>
        <Price prices={trip.prices} />
      </Page>}
    </div>
  );
}

function toSchedule(schedule) {
  return schedule.map(item =>
    <Box key={item.date}>
      <Row>
        <Col xs={2} md={2} lg={1}>
          <Calendar date={item.date} />
        </Col>
        <Col xs={10} md={10} lg={11}>
          <Timeline>
            {toScheduleEvents(item.events)}
          </Timeline>
        </Col>
      </Row>
    </Box>
  );
}

function toScheduleEvents(events) {
  return events.map(event =>
    <Event key={event.time} title={event.time} text={event.text} />
  );
}

function toSightseeing(sights) {
  return sights.map(sight =>
    <Box>
      <SubTitle>{sight.name} {sight.website && <small><a href={sight.website} target="_blank" rel="noopener noreferrer"><FontAwesomeIcon icon={faExternalLinkAlt} /></a></small>}</SubTitle>
      <p><FontAwesomeIcon icon={faMapMarkedAlt} /> {sight.location}</p>
      <Tabs defaultActiveKey="photos" id={`sight-${sight.name}`}>
        <Tab eventKey="photos" title={<span><FontAwesomeIcon icon={faImages} /> Fotos</span>}>
          <Carousel>
            {toCarouselItems(sight.photos)}
          </Carousel>
        </Tab>
        {sight.location && <Tab eventKey="map" title={<span><FontAwesomeIcon icon={faMap} /> Mapa</span>}>
          <iframe
            src={`https://www.google.com/maps/embed/v1/place?key=AIzaSyBdSH-A40U0vFr5fpnsQFYq2cN6PN285FA&maptype=satellite&zoom=18&q=${sight.location}`}
            frameBorder="0" style={{border:0, width: '100%', minHeight: '350px'}} allowFullScreen="" aria-hidden="false"
            tabIndex="0" />
        </Tab>}
      </Tabs>
    </Box>
  );
}

function toFlights(flights) {
  return flights.map(flight =>
    <Box key={flight.flight}>
      <Row>
        <Col xs={2} md={2} lg={1}>
          <Calendar date={flight.date} />
        </Col>
        <Col xs={10} md={10} lg={11}>
          <Row>
            <Col xs={12} md={6}>
              <SubTitle><FontAwesomeIcon icon={faPlaneDeparture} /> {flight.fromAirportName} ({flight.fromAirport})</SubTitle>
              <p><FontAwesomeIcon icon={faClock} /> Decolagem prevista: {flight.departure}</p>
              <p><FontAwesomeIcon icon={faClock} /> Check in: {flight.checkIn}</p>
            </Col>
            <Col xs={12} md={6}>
              <SubTitle><FontAwesomeIcon icon={faPlaneArrival} /> {flight.toAirportName} ({flight.toAirport})</SubTitle>
              <p><FontAwesomeIcon icon={faClock} /> Chegada prevista: {flight.arrival}</p>
              {flight.company && <p><FontAwesomeIcon icon={faPlane} /> {flight.company}</p>}
            </Col>
          </Row>
        </Col>
      </Row>
      <hr />
    </Box>
  );
}

function toRestaurants(restaurants) {
  return restaurants.map(restaurant =>
    <Restaurant key={restaurant.name}>
      <SubTitle>{restaurant.name} {restaurant.website && <small><a href={restaurant.website} target="_blank" rel="noopener noreferrer"><FontAwesomeIcon icon={faExternalLinkAlt} /></a></small>}</SubTitle>
      {restaurant.photos && restaurant.photos.length > 0 && <Carousel>
        {toCarouselItems(restaurant.photos)}
      </Carousel>}
      <Box>
        {restaurant.text.split('\n').map(line => <p key={line}>{line}</p>)}
      </Box>
      {restaurant.prices && <Price prices={restaurant.prices} optional={true} />}
      {restaurant.reviews && restaurant.reviews.length > 0 && <Box>
        <Row>
          {toExternalReviews(restaurant.reviews)}
        </Row>
      </Box>}
    </Restaurant>
  );
}

function toHotel(hotel) {
  return (
    <Box>
      <Title><FontAwesomeIcon icon={faConciergeBell} /> Hotel</Title>
      <h5>{hotel.name} {hotel.website && <small><a href={hotel.website} target="_blank" rel="noopener noreferrer"><FontAwesomeIcon icon={faExternalLinkAlt} /></a></small>}</h5>
      <Carousel>
        {toCarouselItems(hotel.photos)}
      </Carousel>
      {hotel.amenities && <Box>
        <Row>
          {hotel.amenities.outdoors && <Col xs={12} sm={6} md={6} lg={4}>
            <Box>
              <SubTitle><FontAwesomeIcon icon={faLeaf} /> Ar Livre</SubTitle>
              <AmenitiesList>
                {toListItem(hotel.amenities.outdoors)}
              </AmenitiesList>
            </Box>
          </Col>}
          {hotel.amenities.pets && <Col xs={12} sm={6} md={6} lg={4}>
            <Box>
              <SubTitle><FontAwesomeIcon icon={faPaw} /> Animais de Estimação</SubTitle>
              <AmenitiesList>
                {toListItem(hotel.amenities.pets)}
              </AmenitiesList>
            </Box>
          </Col>}
          {hotel.amenities.wellness && <Col xs={12} sm={6} md={6} lg={4}>
            <Box>
              <SubTitle><FontAwesomeIcon icon={faSpa} /> Bem Estar</SubTitle>
              <AmenitiesList>
                {toListItem(hotel.amenities.wellness)}
              </AmenitiesList>
            </Box>
          </Col>}
          {hotel.amenities.activities && <Col xs={12} sm={6} md={6} lg={4}>
            <Box>
              <SubTitle><FontAwesomeIcon icon={faFlag} /> Atividades</SubTitle>
              <AmenitiesList>
                {toListItem(hotel.amenities.activities)}
              </AmenitiesList>
            </Box>
          </Col>}
          {hotel.amenities.pool && <Col xs={12} sm={6} md={6} lg={4}>
            <Box>
              <SubTitle><FontAwesomeIcon icon={faSwimmer} /> Piscina</SubTitle>
              <AmenitiesList>
                {toListItem(hotel.amenities.pool)}
              </AmenitiesList>
            </Box>
          </Col>}
          {hotel.amenities.food && <Col xs={12} sm={6} md={6} lg={4}>
            <Box>
              <SubTitle><FontAwesomeIcon icon={faUtensils} /> Alimentação</SubTitle>
              <AmenitiesList>
                {toListItem(hotel.amenities.food)}
              </AmenitiesList>
            </Box>
          </Col>}
          {hotel.amenities.general && <Col xs={12} sm={6} md={6} lg={4}>
            <Box>
              <SubTitle><FontAwesomeIcon icon={faInfoCircle} /> Geral</SubTitle>
              <AmenitiesList>
                {toListItem(hotel.amenities.general)}
              </AmenitiesList>
            </Box>
          </Col>}
          {hotel.amenities.parking && <Col xs={12} sm={6} md={6} lg={4}>
            <Box>
              <SubTitle><FontAwesomeIcon icon={faParking} /> Estacionamento</SubTitle>
              <AmenitiesList>
                {toListItem(hotel.amenities.parking)}
              </AmenitiesList>
            </Box>
          </Col>}
          {hotel.amenities.internet && <Col xs={12} sm={6} md={6} lg={4}>
            <Box>
              <SubTitle><FontAwesomeIcon icon={faWifi} /> Internet</SubTitle>
              <AmenitiesList>
                {toListItem(hotel.amenities.internet)}
              </AmenitiesList>
            </Box>
          </Col>}
        </Row>
      </Box>}
      {hotel.reviews && hotel.reviews.length > 0 && <Box>
        <Row>
          {toExternalReviews(hotel.reviews)}
        </Row>
      </Box>}
    </Box>);
}

function toExternalReviews(reviews) {
  return reviews.map(review =>
    <Col xs={12} md={6} lg={4} key={review.text}>
      <ExternalReview>
        <ExternalReviewText>"{review.text}"</ExternalReviewText>
        <ExternalReviewAuthor><FontAwesomeIcon icon={faUser} /> {review.author}</ExternalReviewAuthor>
        <ExternalReviewSource>{review.source}</ExternalReviewSource>
      </ExternalReview>
    </Col>
  );
}

function toGroup(name) {
  switch (name.toUpperCase()) {
    case 'IDOSOS':
      return <ElderlyGroup />;
    case 'CASAIS':
      return <PairGroup />;
    case 'FAMILIAS':
      return <FamilyGroup />;
  }
}

function toListItem(amenities) {
  return amenities.map(amenity =>
    <AmenityItem key={amenity}>
      {amenity}
    </AmenityItem>
  );
}

function toCarouselItems(photos) {
  const URL_REGEX = /https?:\/\/([^/]+)\.*/

  return photos.map(photo => {
    const source = URL_REGEX.exec(photo)[1];
    return (<Carousel.Item key={photo}>
      <img
        className="d-block w-100"
        src={photo}
        alt={photo}
      />
      <Carousel.Caption>
        <a href={photo} target="_blank" rel="noopener noreferrer">fonte: {source}</a>
      </Carousel.Caption>
    </Carousel.Item>);
  });
}

function toList(list, symbol) {
  return list.map(item =>
    <li key={item}>{symbol} {item}</li>
  );
}